import React, { useEffect } from "react";
import styled from "@emotion/styled";
import { motion, useAnimation } from "framer-motion";

import ContentWrapper from "../styles/ContentWrapper";
import Underlining from "../styles/Underlining";
import Social from "./Social";
import { Link } from "react-router-dom";

const StyledSection = styled.section`
    width: 100%;
    height: auto;
    background: #121212;
`;

const StyledContentWrapper = styled(ContentWrapper)`
    && {
        width: 100%;
        height: 100%;
        min-height: 60vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 6rem;
        @media (min-width: 768px) {
            margin-bottom: 10rem;
        }
        .greetings {
            font-size: 2.5rem;
            display: flex;
            justify-content: flex-start;
            align-items: center;
        }
        .title {
            font-size: 2rem;
            margin-top: 6rem;
            margin-bottom: 1.5rem;
            @media (min-width: 768px) {
                margin-bottom: 0;
            }
        }
        .subtitle {
            margin-top: -0.75rem;
        }
        .description {
            font-size: 1.125rem;
            margin-bottom: 2rem;
        }
        @media (max-width: 568px) {
            .description {
                width: 85%;
            }
        }
    }
`;

const AnimatedUnderlining = motion(Underlining);

const Introduction = () => {
    const gControls = useAnimation();
    const eControls = useAnimation();
    const sControls = useAnimation();
    const uControls = useAnimation();

    useEffect(() => {
        const pageLoadSequence = async () => {
            eControls.start({
                rotate: [0, -10, 12, -10, 9, 0, 0, 0, 0, 0, 0],
                transition: { duration: 2.5, loop: 3, repeatDelay: 1 },
            });
            await gControls.start({
                opacity: 1,
                y: 0,
                transition: { delay: 0.4 },
            });
            await sControls.start({
                opacity: 1,
                x: 0,
            });
            await uControls.start({
                boxShadow: `inset 0 -2rem 0 "#2A2926"
                    }`,
                transition: { delay: 0.4, ease: "circOut" },
            });
        };
        pageLoadSequence();
    }, [eControls, gControls, sControls, uControls]);

    return (
        <StyledSection>
            <StyledContentWrapper>
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={gControls}
                    data-testid="animated-heading"
                >
                    <h1 className="title">
                        <div className="greetings">
                            안녕하세요 👋
                            <motion.div
                                animate={eControls}
                                style={{ originX: 0.7, originY: 0.7 }}
                            ></motion.div>
                        </div>
                        저는 김유준입니다.
                    </h1>
                    <h3 className="subtitle">
                        <AnimatedUnderlining animate={uControls} big highlight>
                            Front-end Developer
                        </AnimatedUnderlining>
                    </h3>
                    <div className="description">{`
                        3년차 프론트엔드 개발자입니다.
                        클린 코드에 집중하며 개발을 하고있습니다.
                        코드 분석하는 것을 잘하며 좋아합니다.
                        `}</div>
                </motion.div>
                <motion.div initial={{ opacity: 0, x: 20 }} animate={sControls}>
                    <Social
                        fontSize="1.1rem"
                        padding=".6rem 1.55rem .4rem 1.55rem"
                        width="auto"
                    />
                    <Link to="https://solved.ac/profile/youga114">
                        <img src="http://mazassumnida.wtf/api/v2/generate_badge?boj=youga114" />
                    </Link>
                </motion.div>
            </StyledContentWrapper>
        </StyledSection>
    );
};

export default Introduction;
