/* eslint-disable jsx-a11y/alt-text */
import React, { useState, useEffect, useRef } from "react";
import styled from "@emotion/styled";
import { motion, useAnimation } from "framer-motion";

import useOnScreen from "../hooks/useOnScreen";
import ContentWrapper from "../styles/ContentWrapper";
import Button from "../styles/Button";

import album from "../content/projects/icons/album.png";
import joystick from "../content/projects/icons/joystick.png";
import portfolio from "../content/projects/icons/portfolio.png";
import notebook from "../content/projects/icons/notebook.png";
import tetris from "../content/projects/icons/tetris.png";
import website from "../content/projects/icons/website.png";
import mobile from "../content/projects/icons/mobile.png";

const StyledSection = styled.section`
    width: 100%;
    height: auto;
    margin-top: 6rem;
`;

const StyledContentWrapper = styled(ContentWrapper)`
    && {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-right: 0;
        padding-left: 0;
        @media (min-width: 992px) {
            padding-right: 2.5rem;
            padding-left: 2.5rem;
        }
        .section-title {
            padding-right: 2.5rem;
            padding-left: 2.5rem;
            @media (min-width: 992px) {
                padding-right: 0;
                padding-left: 0;
            }
        }
    }
`;

const StyledProjects = styled.div`
    display: grid;
    grid-template-columns: repeat(3, 15.625rem);
    grid-template-rows: repeat(2, auto);
    grid-auto-flow: column;
    column-gap: 1rem;
    row-gap: 1rem;
    padding: 0 2rem;
    overflow-x: scroll;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar {
        display: none;
    }
    &::after {
        content: "";
        width: "17.125rem";
    }
    @media (min-width: 992px) {
        grid-auto-flow: row;
        grid-template-columns: repeat(3, 15.625rem);
        overflow: visible;
        padding: 0;
    }
    @media (hover: hover) {
        scrollbar-color: rgba(255, 255, 255, 0.5) transparent; // Firefox only
        &::-webkit-scrollbar {
            display: block;
            -webkit-appearance: none;
        }

        &::-webkit-scrollbar:horizontal {
            height: 0.8rem;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 8px;
            border: 0.2rem solid #121212;
            background-color: rgba(255, 255, 255, 0.5);
        }

        &::-webkit-scrollbar-track {
            background-color: #121212;
            border-radius: 8px;
        }
    }

    .interest {
        width: 13.625rem;
        height: 1rem;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 1rem;
        border: 0.125rem solid #fafafa;
        border-radius: 1rem;
        background: #1c1c1c;
        .icon {
            width: 1.5rem;
            height: 1.5rem;
            margin-right: 0.5rem;
        }
    }
`;

const projects = [
    {
        name: "Among",
        icon: album,
    },
    {
        name: "React Portfolio",
        icon: portfolio,
    },
    {
        name: "Web Tetris",
        icon: tetris,
    },
    {
        name: "Many As One",
        icon: notebook,
    },
    {
        name: "Unreal TPS Game",
        icon: joystick,
    },
    {
        name: "사진을 활용한 검색 시스템",
        icon: mobile,
    },
    {
        name: "마을마켓",
        icon: website,
    },
];

const Projects = () => {
    const [shownProjects, setShownProjects] = useState(5);

    const ref = useRef<HTMLDivElement | null>(null);
    const onScreen = useOnScreen(ref);

    const iControls = useAnimation();
    const bControls = useAnimation();

    useEffect(() => {
        const sequence = async () => {
            if (onScreen) {
                // i receives the value of the custom prop - can be used to stagger
                // the animation of each "interest" element
                await iControls.start((i) => ({
                    opacity: 1,
                    scaleY: 1,
                    transition: { delay: i * 0.1 },
                }));
                await bControls.start({ opacity: 1, scaleY: 1 });
            }
        };
        sequence();
    }, [onScreen, shownProjects, iControls, bControls]);

    const showMoreItems = () => setShownProjects(shownProjects + 4);

    return (
        <StyledSection id="projects">
            <StyledContentWrapper>
                <h3 className="section-title">Projects</h3>
                <StyledProjects ref={ref}>
                    {projects
                        .slice(0, shownProjects)
                        .map(({ name, icon }, key) => (
                            <motion.div
                                className="interest"
                                key={key}
                                custom={key}
                                initial={{ opacity: 0, scaleY: 0 }}
                                animate={iControls}
                            >
                                <img className="icon" src={icon} /> {name}
                            </motion.div>
                        ))}
                    {shownProjects < projects.length && (
                        <motion.div
                            initial={{ opacity: 0, scaleY: 0 }}
                            animate={bControls}
                        >
                            <Button onClick={() => showMoreItems()}>
                                + Load more
                            </Button>
                        </motion.div>
                    )}
                </StyledProjects>
            </StyledContentWrapper>
        </StyledSection>
    );
};

export default Projects;
