import React from "react";
import { Link } from "react-router-dom";
import styled from "@emotion/styled";
import { animateScroll } from "react-scroll";

const StyledNav = styled.nav`
    display: none;
    @media (min-width: 1200px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 31.25rem;
        background: #121212;
        a {
            color: white;
        }
    }
    .nav-link {
        font-size: 1.2rem;
        font-weight: 700;
        text-align: center;
        position: relative;
        padding: 0;
        &::before {
            transition: 200ms ease-out;
            height: 0.1563rem;
            content: "";
            position: absolute;
            background-color: white;
            width: 0%;
            bottom: -0.125rem;
        }
        &:hover::before {
            width: 100%;
        }
    }
`;

const moveToHome = () => {
    animateScroll.scrollToTop({
        duration: 1000,
        delay: 0,
        smooth: "easeInOutQuad",
    });
};

const moveToAbout = () => {
    animateScroll.scrollTo(1100, {
        duration: 1000,
        delay: 0,
        smooth: "easeInOutQuad",
    });
};

const moveToProjects = () => {
    animateScroll.scrollTo(1700, {
        duration: 1000,
        delay: 0,
        smooth: "easeInOutQuad",
    });
};

const moveToCareer = () => {
    animateScroll.scrollTo(6500, {
        duration: 1000,
        delay: 0,
        smooth: "easeInOutQuad",
    });
};

const moveToContact = () => {
    animateScroll.scrollTo(9400, {
        duration: 1000,
        delay: 0,
        smooth: "easeInOutQuad",
    });
};

const Navigationbar = () => {
    return (
        <StyledNav>
            <Link to="/" className="nav-link" onClick={moveToHome}>
                Home
            </Link>
            <Link to="/" className="nav-link" onClick={moveToAbout}>
                About Me
            </Link>
            <Link to="/" className="nav-link" onClick={moveToProjects}>
                Projects
            </Link>
            <Link to="/" className="nav-link" onClick={moveToCareer}>
                Career
            </Link>
            <Link to="/" className="nav-link" onClick={moveToContact}>
                Contact
            </Link>
        </StyledNav>
    );
};

export default Navigationbar;
