const DetailedProjects = () => {
    const [onScreen, setOnScreen] = useState<any>({});
    const handleOnScreen = (el: string) => {
        if (!onScreen[el]) {
            const updatedOnScreen = { ...onScreen };
            updatedOnScreen[el] = true;
            setOnScreen(updatedOnScreen);
        }
    };
    const pVariants = {
        hidden: { opacity: 0, y: 20 },
        visible: { opacity: 1, y: 0 },
    };

    useEffect(() => {
        let initial: any = {};
        projects.forEach((project) => {
            initial[project.position] = false;
        });
        setOnScreen(initial);
    }, []);

    const tRef = useRef<HTMLDivElement | null>(null);
    const tOnScreen = useOnScreen(tRef);
    const tVariants = {
        hidden: { opacity: 0 },
        visible: { opacity: 1 },
    };

    return (
        <StyledSection id="projects">
            <StyledContentWrapper>
                <motion.div
                    ref={tRef}
                    variants={tVariants}
                    animate={tOnScreen ? "visible" : "hidden"}
                >
                    <h3 className="section-title">Detailed Projects</h3>
                </motion.div>
                <div className="projects">
                    {projects.map((project, key) => {
                        return (
                            <VisibilitySensor
                                key={key}
                                onChange={() =>
                                    handleOnScreen((key + 1).toString())
                                }
                                partialVisibility={true}
                                minTopValue={100}
                            >
                                <StyledProject
                                    position={key + 1}
                                    variants={pVariants}
                                    animate={
                                        onScreen[project.position]
                                            ? "visible"
                                            : "hidden"
                                    }
                                >
                                    <div className="details">
                                        <div className="category">
                                            <img
                                                className="img"
                                                src={project.icon}
                                                alt=""
                                            />{" "}
                                            {project.category}
                                        </div>
                                        <div className="title">
                                            {project.title}
                                        </div>
                                        <div className="date">
                                            <div>{project.date}</div>
                                            <div>{project.numberOfPeople}</div>
                                        </div>
                                        <div>{project.body}</div>
                                        <div className="tags">
                                            {project.skills.map(
                                                (skill, index) => {
                                                    return (
                                                        <Underlining
                                                            big={false}
                                                            highlight
                                                            key={index}
                                                        >
                                                            {skill}
                                                        </Underlining>
                                                    );
                                                }
                                            )}
                                        </div>
                                        <div className="links">
                                            {project.github && (
                                                <a
                                                    href={project.github}
                                                    target="_blank"
                                                    rel="nofollow noopener noreferrer"
                                                    aria-label="External Link"
                                                >
                                                    <Icon
                                                        name="github"
                                                        color="#AAAAAA"
                                                    />
                                                </a>
                                            )}
                                            {project.external && (
                                                <Link to={project.external}>
                                                    <Icon
                                                        name="external"
                                                        color="#AAAAAA"
                                                    />
                                                </Link>
                                            )}
                                            {project.youtube && (
                                                <a
                                                    href={project.youtube}
                                                    target="_blank"
                                                    rel="nofollow noopener noreferrer"
                                                >
                                                    <Icon
                                                        name="youtube"
                                                        color="#AAAAAA"
                                                    />
                                                </a>
                                            )}
                                        </div>
                                    </div>
                                    <VisibilitySensor>
                                        <Image
                                            src={project.image}
                                            alt=""
                                            className="screenshot"
                                        />
                                    </VisibilitySensor>
                                </StyledProject>
                            </VisibilitySensor>
                        );
                    })}
                </div>
            </StyledContentWrapper>
        </StyledSection>
    );
};

export default DetailedProjects;
