const Career = () => {
    const [visibleCareer, setVisibleCareer] = useState(1);

    const [onScreen, setOnScreen] = useState<any>({});
    const handleOnScreen = (el: string) => {
        if (!onScreen[el]) {
            const updatedOnScreen = { ...onScreen };
            updatedOnScreen[el] = true;
            setOnScreen(updatedOnScreen);
        }
    };
    const pVariants = {
        hidden: { opacity: 0, y: 20 },
        visible: { opacity: 1, y: 0 },
    };

    useEffect(() => {
        setVisibleCareer(1);
        let initial: any = {};
        career.forEach((project) => {
            initial[project.position] = false;
        });
        setOnScreen(initial);
    }, []);

    const tRef = useRef<HTMLDivElement | null>(null);
    const tOnScreen = useOnScreen(tRef);
    const tVariants = {
        hidden: { opacity: 0 },
        visible: { opacity: 1 },
    };

    const bRef = useRef<any>(null);
    const bOnScreen = useOnScreen(bRef);
    const bVariants = {
        hidden: { opacity: 0 },
        visible: { opacity: 1 },
    };

    return (
        <StyledSection id="projects">
            <StyledContentWrapper>
                <motion.div
                    ref={tRef}
                    variants={tVariants}
                    animate={tOnScreen ? "visible" : "hidden"}
                >
                    <h3 className="section-title">Career</h3>
                </motion.div>
                <div className="projects">
                    <VisibilitySensor
                        onChange={() => handleOnScreen((1).toString())}
                        partialVisibility={true}
                        minTopValue={100}
                    >
                        <StyledProject
                            position={1}
                            variants={pVariants}
                            animate={onScreen[1] ? "visible" : "hidden"}
                        >
                            <div className="history">
                                <div
                                    className="details"
                                    style={{ marginBottom: "2rem" }}
                                >
                                    <div className="mainTitle">
                                        (주)더블다운인터액티브
                                    </div>
                                    <div className="date">
                                        2020년 2월 ~ 재직중
                                    </div>
                                    <div className="content">
                                        세계적인 소셜 카지노 회사
                                        더블다운인터랙티브에서 클라이언트
                                        개발자로 일해오면서 웹 카지노 게임,
                                        Unity 카지노 앱 게임, Unity SDK 등
                                        서비스의 여러 프로젝트에 참여하고
                                        있습니다.
                                    </div>
                                </div>
                                <motion.a
                                    ref={bRef}
                                    variants={bVariants}
                                    animate={bOnScreen ? "visible" : "hidden"}
                                    className="cta-btn"
                                    href="https://www.doubledowninteractive.co.kr/kor/main/main.html"
                                    target="_blank"
                                    rel="nofollow noopener noreferrer"
                                    aria-label="External Link"
                                >
                                    <Button textAlign="center">
                                        VISIT WEBSITE
                                    </Button>
                                </motion.a>
                                {career.map((project, index) => {
                                    return (
                                        <div
                                            className="details"
                                            style={{
                                                borderTop: "1px solid white",
                                            }}
                                            key={index}
                                        >
                                            <div className="title">
                                                {project.title}
                                            </div>
                                            <div className="date">
                                                {project.date}
                                            </div>
                                            <div className="description">
                                                {project.description}
                                            </div>
                                            <div className="content">
                                                {project.body.map(
                                                    (body, index) => (
                                                        <div key={index}>
                                                            · {body}
                                                        </div>
                                                    )
                                                )}
                                            </div>
                                        </div>
                                    );
                                })}
                            </div>
                            <div className="imageContainer">
                                <VisibilitySensor
                                    onChange={() => setVisibleCareer(1)}
                                >
                                    <Image
                                        src={doubledownImage}
                                        alt=""
                                        className="screenshot"
                                    />
                                </VisibilitySensor>
                            </div>
                        </StyledProject>
                    </VisibilitySensor>
                </div>
            </StyledContentWrapper>
        </StyledSection>
    );
};

